* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

:root {
  font-family: "Roboto"; }

/*HEADER*/
.image_container {
  background-image: url("../images/cover.jpeg");
  max-width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: center center;
  display: flex; }

.header_menu_container {
  background-image: url("../images/ico-menu.svg");
  background-color: #0271bc;
  background-repeat: no-repeat;
  background-position: center center;
  height: 22px;
  width: 37px;
  position: fixed; }

.header_text_container {
  color: white;
  display: flex;
  text-align: center;
  justify-content: flex-end;
  gap: 15px;
  flex-direction: column;
  padding-bottom: 25px; }

.h1_text {
  font-size: 42px;
  font-family: "RobotoSlab";
  text-transform: uppercase; }

.button_circle {
  background-image: url("../images/ico-scroll-down.svg");
  border-radius: 50%;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: white;
  width: 40px;
  height: 40px;
  position: relative;
  margin: 0 auto; }

/*MAIN*/
.first_section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 320px;
  height: 376px;
  margin-left: 15px;
  margin-right: 15px;
  gap: 15px;
  line-height: 30px; }

.first_text {
  color: #8ccbea;
  font-size: 14px;
  text-transform: capitalize; }

.second_text {
  font-size: 24px;
  font-family: "RobotoSlab";
  font-weight: bold;
  text-transform: capitalize;
  word-spacing: 3vw;
  /*REVISAR, NO es la mejor manera*/ }

.third_text {
  color: #4a4a4a;
  font-size: 18px; }

.go_button {
  color: #f26002;
  font-size: 20px;
  font-weight: bold;
  text-transform: capitalize;
  border: 2.5px solid #f26002;
  width: 76px;
  height: 45px;
  border-radius: 40px;
  padding-top: 4px;
  margin-top: 15px;
  text-align: center; }
